<template>
    <div
        class="area"
        :style="{ height: size.height + 'px', width: size.width + 'px' }"
    >
        <div
            v-show="bottle.show"
            class="bottle"
            @touchstart.stop="down"
            @touchmove.stop="move"
            @touchend.stop="end"
            :style="{
                left: offset.x + 'px',
                top: offset.y + 'px',
                zIndex: zIndex,
            }"
        >
            <div
                v-for="(wine, index) in bottle.wines"
                :key="index"
                :style="{ backgroundColor: wine }"
                class="wine"
            ></div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Bottle",
    props: ["bottle", "pour"],
    data() {
        return {
            flags: false,
            zIndex: 10,
            // 组件偏离距离
            offset: {
                x: 0,
                y: 0,
                // 最大不可超过屏幕边界
                minx: 0,
                maxx: 0,
                miny: 0,
                maxy: 0,
            },

            // 组件大小
            size: {
                height: 100,
                width: 40,
            },

            // 组件绝对位置
            origin: {
                x: 0,
                y: 0,
            },

            // 鼠标位置
            client: {
                x: 0,
                y: 0,
                // 鼠标第一次点击div的绝对位置
                originx: 0,
                originy: 0,
            },
        };
    },
    mounted() {
        this.origin.x = this.$el.offsetLeft;
        this.origin.y = this.$el.offsetTop;
        this.offset.minx = 0 - this.origin.x;
        this.offset.miny = 0 - this.origin.y;
        this.offset.maxx =
            window.screen.width - this.size.width - this.origin.x;
        this.offset.maxy =
            window.screen.height - this.size.height - this.origin.y;
    },
    methods: {
        down(event) {
            event.preventDefault();

            this.flags = true;
            var touch = event.touches[0];
            this.client.originx = touch.clientX;
            this.client.originy = touch.clientY;
            this.zIndex = 990;
        },
        move(event) {
            event.preventDefault();
            if (this.flags) {
                var touch = event.touches[0];

                this.client.x = touch.clientX;
                this.client.y = touch.clientY;

                // 相对位置
                let x = this.client.x - this.client.originx;
                let y = this.client.y - this.client.originy;

                // 防止出界
                x = Math.max(x, this.offset.minx);
                x = Math.min(x, this.offset.maxx);
                y = Math.max(y, this.offset.miny);
                y = Math.min(y, this.offset.maxy);

                //
                this.offset.x = x;
                this.offset.y = y;
            }
        },
        //鼠标释放时候的函数
        end() {
            console.log("end");
            this.flags = false;
            this.pour(
                {
                    x: this.client.x - (this.client.originx - this.origin.x),
                    y: this.client.y - (this.client.originy - this.origin.y),
                },
                this.bottle.id
            );
            this.offset.x = 0;
            this.offset.y = 0;
            this.zIndex = 10;
        },
    },
};
</script>

<style scoped>
.area {
    border: dotted 1px black;
}

.bottle {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
    border: 1px solid black;
}

.wine {
    height: 22%;
    width: 100%;
}
</style>