<template>
    <div class="main">
        <div class="game-area">
            <div class="container">
                <BottleM
                    v-for="(bottle, index) in bottleArr"
                    :key="index"
                    :pour="pour"
                    :bottle="bottle"
                />
            </div>
            <div class="msg">
                <div>
                    <img class="logo" alt="Vue logo" src="@/assets/logo.png" />
                    <p>当前倒水次数{{ cnt }}</p>
                    <button @click.stop="restart">点我重玩</button>
                    <h1>{{ msg }}</h1>
                </div>
                <div>
                    <p>倒水示意图</p>
                    <img src="@/assets/1.png" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BottleM from "@/components/BottleM";
import { nanoid } from "nanoid";
export default {
    name: "BottleGameM",
    components: { BottleM },

    data() {
        return {
            msg: "池水电费",
            cnt: 0,
            bottleArr: [],
            bottleFloat: {
                id: "",
                show: false,
                wines: [],
            },
        };
    },

    created() {
        for (let i = 0; i < 14; i++) {
            let bottle = {
                id: nanoid(),
                show: true,
                wines: [],
            };
            this.bottleArr.push(bottle);
        }
        this.load();
    },

    methods: {
        pour(touch, id) {
            console.log("pour");
            console.log(touch, id);
            let obj_id;
            this.$children.forEach((c) => {
                if (
                    touch.x > c.origin.x &&
                    touch.x < c.origin.x + c.size.width &&
                    touch.y > c.origin.y &&
                    touch.y < c.origin.y + c.size.height
                ) {
                    console.log(c.bottle.id);
                    if (c.bottle.id != id) obj_id = c.bottle.id;
                }
            });
            if (obj_id) {
                let outBottle, inBottle;
                this.bottleArr.forEach((b) => {
                    if (b.id == obj_id) inBottle = b;
                    if (b.id == id) outBottle = b;
                });
                if (outBottle.wines && inBottle.wines.length < 4) {
                    console.log("ok");
                    inBottle.wines.unshift(outBottle.wines.shift());
                    console.log(inBottle.wines, outBottle.wines);
                    this.cnt++;
                }
            }
            if (this.isWin()) this.msg = "恭喜通关（乐）";
        },
        load() {
            let cs = [
                "blue",
                "gray",
                "red",
                "purple",
                "pink",
                "olive",
                "green",
                "orange",
                "yellow",
                "lightblue",
                "navy",
                "greenyellow",
            ];
            let cs_temp = [];
            cs.forEach((c) => {
                for (let i = 0; i < 4; i++) cs_temp.push(c);
            });
            console.log(cs_temp);
            this.shuffleSelf(cs_temp);
            this.bottleArr.forEach((b, i) => {
                b.wines = [];
                if (i < 12)
                    for (let j = 0; j < 4; j++)
                        b.wines.push(cs_temp[i * 4 + j]);
            });
        },
        shuffleSelf(array) {
            var index = -1,
                length = array.length;
            while (++index < length) {
                // var rand = baseRandom(index, lastIndex),
                var rand = index + Math.floor(Math.random() * (length - index));
                var value = array[rand];

                array[rand] = array[index];

                array[index] = value;
            }
            return array;
        },
        isWin() {
            // 检测是否胜利
            let flag = 1;
            this.bottleArr.some((b) => {
                if (b.wines.length != 0 && b.wines.length != 4) {
                    flag = 0;
                    return true;
                }
                if (b.wines) {
                    let s = b.wines[0];
                    b.wines.forEach((e) => {
                        if (e != s) {
                            flag = 0;
                        }
                    });
                    if (!flag) return true;
                }
            });
            return flag;
        },
        restart() {
            this.cnt = 0;
            this.msg = "";
            this.load();
        },
    },
};
</script>

<style scoped>
.logo {
    width: 60px;
    height: 60px;
}
button {
    height: 60px;
    width: 60px;
    font-size: 20px;
}
.main {
    width: 100%;
    height: 600px;
    padding-top: 60px;
}
.game-area {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
.container {
    display: grid;
    grid-template-rows: repeat(2, 100px);
    grid-template-columns: repeat(7, 40px);
    gap: 10px;
    justify-content: center;
}
.msg {
    display: flex;
    justify-content: space-around;
    width: 100%;
}
</style>